.nowebp .imagen-contacto{
    background-image: url('../img/encuentra.png');
}
.webp .imagen-contacto{
    background-image: url('../img/encuentra.webp');
}
.imagen-contacto{
    background-position: center center;
    background-size: cover;
    padding: calc($separacion * 2) 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    h2{
        font-size: 2rem;
        font-weight: $black;
        color: $blanco;

    }
    
    p{
        font-size: 1.8rem;
        color: $blanco;
    }
}